@use "./variables" as *;
@use "./mixins" as *;

@media screen and (min-width:720px ){
    .sidebar-for-720{
        display: none;
    }
}

@media screen and (max-width: 720px){
    .content{
        padding-left: 2vw;
        @include size($width-100, 253.35vh);
    }

    .filters-default, .sidebar-for-720__checkbox--disabled{
        display: none;
    }

    .product-card{
        @include size(46vw, 25vh);
    }
}